import React,{Component} from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom'; 
import urlConst from '../config/config';
import Background from '../resources/dashboard_image.jpg';

export default class UserPage extends Component{
state: {users: any[]} = {
    users: []
}
    componentDidMount(){
        axios.get(urlConst.url +'/users',
         { headers: {'authorization': `${localStorage.token}`}}
        )
        .then(resp =>{
            this.setState({
                users:resp.data.userList
            });
        });
    }
    viewNote(id: number){
        localStorage.setItem('user_id_forNotes', `${id}`);   
    }
    render(){
        return(       
            <div style={{ backgroundColor: "black", backgroundImage: `url(${Background})`, height: "657px", backgroundSize: 'cover', backgroundAttachment:'fixed' }}>     
            <div className='container' style={{width:'900px'}}>
                <br/><br/>
                <div>
                <h3 style={{ color: "#ffe20f",position: 'fixed', left: '50px' }}>Users List</h3>
                <Link to="/dashboard" className='btn btn-outline-primary' style={{position: 'fixed',right: '150px'}}>Go to Dashboard</Link>
                <Link to="/" className='btn btn-danger' style={{position: 'fixed',right: '34px'}}>Sign Out</Link>
                </div>
                <br/><br/><br/><br/>         
                <div className='row' style={{overflow:'scroll', height:480,width:'900px', overflowX:'hidden'}}>
                    {this.state.users.map(user => {
                        return(
                            <div className='col col-12' key={user._id}>
                                <div className='card'>
                                     <div className='card-header' style={{backgroundColor:'#EAD0FF'}}>
                                        <div className='row'>
                                            <div className="col-12">
                                                <div className='row'>
                                                    <div className="col-9">
                                                        <h3>{user.firstname} {user.lastname} </h3>
                                                    </div>
                                                    <div className="col-3">
                                                        <Link to="/allnotes"  onClick={()=> this.viewNote(user._id)} className='btn btn-info btn-sm' style={{float:'right'}}  >View Notes</Link>
                                                    </div>
                                                </div>
                                            </div>                                           
                                        </div>
                                    </div>                                    
                                    <div className="row card-body ">
                                        <div className="col-6">
                                            <p><b>Email: </b>{user.email}</p> 
                                        </div>
                                        <div className="col-6">
                                            <p><b> Mobile Number: </b>{user.mobileNumber}</p>
                                        </div>
                                    </div>                                    
                                </div>                                
                                <br/> 
                            </div>
                        )
                    })}
                </div>
            </div>      
          </div>
        )
    }
}